.reviews {
    background-image: url('../image/background-reviews.png');
    background-size: cover;
    background-repeat: no-repeat;
}

.reviews-list {
    display: flex;
    justify-content: space-between;
    list-style: none;
    margin: 48px 0 32px;
    padding-left: 0;
    gap: 24px;

    .review-item {
        padding: 32px;
        max-width: 648px;
        background-color: #FFF;

        .review_top {
            display: flex;
            align-items: center;
            justify-content: space-between;

            .review_top-avatar {
                display: flex;
                align-items: center;
                gap: 24px;

                div {
                    h3 {
                        margin-bottom: 4px;
                        font-weight: 300;
                        font-size: 14px;
                        line-height: 120%;
                    }

                    h2 {
                        margin-top: 0;
                        font-weight: 700;
                        font-size: 20px;
                        line-height: 150%;
                    }
                }
            }
        }

        .review_main {
            p {
                font-weight: 400;
                font-size: 14px;
                line-height: 150%;
            }
        }
    }
}