.footer {
    padding: 80px 0 0;
    color: #fff;
    background-color: #222;

    .container {
        .footer-top {
            display: flex;
            align-items: center;
            justify-content: space-between;
            margin-bottom: 20px;
            gap: 150px;

            .logo {
                min-width: 276px;
            }
        }

        .footer-main {
            display: flex;
            flex-wrap: wrap;
            justify-content: space-between;
            margin-top: 70px;
            margin-bottom: 70px;
            gap: 24px;

            .footer-contacts {
                display: flex;
                flex-direction: column;
                padding-left: 0;
                gap: 24px;

                .footer-contacts-item {
                    display: flex;
                    align-items: center;
                    gap: 24px;

                    img {
                        padding: 12px 11px;
                        background-color: #2C2C2C;
                    }

                    h3 {
                        margin: 0;
                        padding: 0;
                        font-weight: 700;
                        font-size: 12px;
                        line-height: 120%;
                        text-transform: uppercase;
                        color: rgb(140, 140, 140);
                    }

                    h2 {
                        margin: 0;
                        font-weight: 700;
                        font-size: 16px;
                        line-height: 150%;
                        color: rgb(255, 255, 255);
                    }
                }
            }

            .navigation-footer {
                ul {
                    display: flex;
                    flex-direction: column;
                    list-style: none;
                    padding-left: 0;
                    gap: 24px;

                    li {
                        a {
                            font-weight: 700;
                            font-size: 14px;
                            line-height: 120%;
                            text-transform: uppercase;
                            transition: .5s;

                            &:hover {
                                color: var(--blue-color);
                            }
                        }
                    }
                }
            }

            .footer-comment {
                max-width: 424px;

                .footer-avatar {
                    display: flex;
                    align-items: center;
                    gap: 24px;

                    h3 {
                        margin: 0;
                        margin-bottom: 8px;
                    }

                    p {
                       margin: 0; 
                    }
                }

                .footer-comment-text {
                    margin-bottom: 20px;
                    font-weight: 400;
                    font-size: 14px;
                    line-height: 150%;
                }
            }
        }

    }

    .footer-bottom {
        margin-top: 30px;
        padding: 16px 0;
        background-color: #181818;

        .container {
            display: flex;
            align-items: center;
            justify-content: space-between;
        }
    }

    ul {
        list-style: none;
    }
}