.hero {
    margin-top: -101px;
    background-image: url('../image/background-hero.png');
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;

    .container {
        .hero__content {
            padding: 142px 0 345px;
            text-align: center;

            .hero_subtitle {
                font-weight: 300;
                font-size: 30px;
                line-height: 150%;
                text-transform: uppercase;
                color: rgb(255, 255, 255);
            }
    
            .hero_title {
                font-weight: 600;
                font-size: 56px;
                line-height: 150%;
                text-transform: uppercase;
                color: rgb(79, 143, 240);
            }
    
            .hero_description {
                font-weight: 400;
                font-size: 20px;
                line-height: 120%;
                color: var(--light-color);
            }
        }
    }
}