.header {
    color: #fff;
    background-color: #010101;

    .container {
        .header__top {
            display: flex;
            align-items: center;
            justify-content: space-between;
            padding: 16px 0;

            .call_link {
                color: var(--blue-color);
            }
        }
    }
}

.logo {
    font-weight: 900;
    font-size: 28px;
    line-height: 75%;
    color: rgb(79, 143, 240);

    span {
        font-weight: 900;
        font-size: 20px;
        text-transform: lowercase;
        color: rgb(255, 255, 255);
    }
}

.header__main {
    position: sticky;
    top: 0;
    padding: 22px 0 28px;
    color: #fff;
    background: rgba(1, 1, 1, 0.319);

    .container {
        display: flex;
        align-items: center;
        justify-content: space-between;
        gap: 20px;

        nav {
            ul {
                display: flex;
                list-style: none;
                gap: 32px;
    
                li {
                    a {
                        position: relative;
                        text-decoration: none;
                        color: inherit;
                        transition: .4s;

                        &::before {
                            content: "";
                            position: absolute;
                            left: 0;
                            bottom: 0;
                            width: 100%;
                            height: 1px;
                            background-color: var(--blue-color);
                            transform: scaleX(0);
                            transform-origin: left;
                            transition: .6s;
                        }

                        &:hover {
                            color: var(--blue-color);

                            &::before {
                                transform: scaleX(1);
                            }
                        }

                        
                    }
                }
            }
        }
    }
}

.header-phone-link, .header-mail-link {
    position: relative;
    margin-left: calc(29px + 16px);
}

.header-phone-link::before, .header-mail-link::before {
    position: absolute;
    left: -32px;
    top: -2%;
    width: 16px;
    height: auto;
}

.header-phone-link::before {
    content: url("../image/phone-icon.png");
}

.header-mail-link::before {
    content: url("../image/mail-icon.png");
}