.posts_list {
    display: grid;
    grid-template-columns: repeat(3,minmax(300px, 424px));
    list-style: none;
    margin-top: 30px;
    margin-left: 0;
    padding-left: 0;
    gap: 24px;

    .posts_item {
        a {
            img {
                max-width: 424px;
                max-height: 270px;
            }

            .post-title {
                font-weight: 700;
                font-size: 16px;
                line-height: 150%;
            }

            .post-date {
                font-weight: 300;
                font-size: 12px;
                line-height: 120%;
                color: rgb(34, 34, 34);
            }

            .post-desc {
                font-weight: 400;
                font-size: 14px;
                line-height: 120%;
            }

        }
    }
}