.our_services {
    background-color: rgb(247, 247, 247);
}

.our_services__list {
    display: grid;
    grid-template-columns: repeat(3, minmax(280px, 1fr));
    list-style: none;
    margin: 0;
    margin-top: 48px;
    padding: 0;
    row-gap: 20px;
}

.our_service-item {
    padding: 60px 32px;
    max-width: 424px;
    text-align: center;
    background-color: #FFF;

    img {
        max-width: 35px;
        max-height: 35px;
    }

    h3 {
        margin-bottom: 0;
        font-weight: 800;
        font-size: 20px;
        line-height: 150%;
        color: rgb(34, 34, 34);
    }

    p {
        font-weight: 400;
        font-size: 14px;
        line-height: 120%;
        color: rgb(34, 34, 34);
    }
}