.about {
    .container {
        display: flex;

        .about-content {
            h2 {
                margin-bottom: 60px;
            }

            img {
                width: 100%;
                max-width: 563px;
            }

            p {
                font-weight: 400;
                font-size: 16px;
                line-height: 150%;
                color: rgb(34, 34, 34);
            }

            .btn-flex {
                display: flex;
                margin-top: 32px;
                gap: 24px;
            }
        }
    }
}