// базовые подключения
@import "vars";
@import "mixins";
@import "fonts";
@import "settings";

// подключения компонентов страницы
@import "./components/header";
@import "./components/footer";
@import "./components/hero";
@import "./components/ourServices";
@import "./components/about";
@import "./components/team";
@import "./components/reviews";
@import "./components/posts";

a {
  color: inherit;
}

.block {
  padding: 80px 0;
}

.title-block {
  position: relative;
  font-weight: 800;
  font-size: 28px;
  line-height: 150%;
  text-transform: uppercase;
  color: rgb(34, 34, 34);

  &.title-left {
    &::before {
      left: calc(0% + 40px);
    }
  }

  &::before {
    content: "";
    position: absolute;
    left: 50%;
    bottom: -20px;
    width: 80px;
    height: 3px;
    background-color: var(--blue-color);
    transform: translateX(-50%);
  }
}

.subtitle-block {
  margin-top: 20px;
  max-width: 900px;
  font-weight: 400;
  font-size: 16px;
  line-height: 120%;
  color: rgb(34, 34, 34);
}

.center-block {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
}

.btn {
  border: 0;
  padding: 16px 24px;
  text-transform: uppercase;
  cursor: pointer;

  &.btn--blue {
    border: 2px solid rgb(79, 143, 240);
    color: var(--light-color);
    background-color: var(--blue-color);

    &:hover {
      background-color: var(--dark-blue-color);
    }
  }
}

.overflow-hidden {
  overflow: hidden;

  img {
    transition: transform 0.5s ease;

    &:hover {
      transform: scale(1.15);
    }
  }
}