.team_list {
    display: flex;
    list-style: none;
    margin-top: 30px;
    padding-left: 0;
    gap: 24px;

    .team-item {
        max-width: 312px;
        text-align: center;
        background-color: #fff;

        .overflow-hidden {
            position: relative;
            z-index: 10;
        }
        
        .team-item-content {
            position: relative;
            z-index: 50; 
            overflow: hidden;
            margin-top: -5px;
            padding: 24px 16px;
            background-color: #fff;
        }

        h2 {
            margin-bottom: 10px;
            font-weight: 700;
            font-size: 16px;
            line-height: 120%;
        }

        p {
            margin-top: 0;
            font-weight: 300;
            font-size: 14px;
            line-height: 120%;
        }
    }
}