@import 'https://fonts.googleapis.com/css2?family=Nunito+Sans:ital,opsz,wght@0,6..12,200..1000;1,6..12,200..1000&display=swap';
:root {
  --font-family: "Nunito Sans", sans-serif;
  --content-width: 1320px;
  --container-offset: 15px;
  --container-width: calc(var(--content-width) + (var(--container-offset) * 2));
  --light-color: #fff;
  --blue-color: #4F8FF0;
  --dark-blue-color: #3b78d3;
  --dark-color: #2C2C2C;
  --black-text-color: #222;
}

/* stylelint-disable */
/* stylelint-disable */
/* stylelint-disable */
.custom-checkbox__field:checked + .custom-checkbox__content::after {
  opacity: 1;
}

.custom-checkbox__field:focus + .custom-checkbox__content::before {
  outline: 2px solid #f00;
  outline-offset: 2px;
}

.custom-checkbox__field:disabled + .custom-checkbox__content {
  opacity: 0.4;
  pointer-events: none;
}

/* stylelint-disable */
/* stylelint-disable */
/* stylelint-disable value-keyword-case */
*,
*::before,
*::after {
  box-sizing: border-box;
}

.page {
  height: 100%;
  font-family: var(--font-family, sans-serif);
  -webkit-text-size-adjust: 100%;
  scroll-behavior: smooth;
}

.page__body {
  margin: 0;
  min-width: 360px;
  min-height: 100%;
  font-size: 16px;
}

img {
  height: auto;
  max-width: 100%;
  object-fit: cover;
}

a {
  text-decoration: none;
}

.site-container {
  overflow: hidden;
}

.is-hidden {
  display: none !important; /* stylelint-disable-line declaration-no-important */
}

.btn-reset {
  border: none;
  padding: 0;
  background-color: transparent;
  cursor: pointer;
}

.list-reset {
  list-style: none;
  margin: 0;
  padding: 0;
}

.input-reset {
  -webkit-appearance: none;
  appearance: none;
  border: none;
  border-radius: 0;
  background-color: #fff;
}
.input-reset::-webkit-search-decoration, .input-reset::-webkit-search-cancel-button, .input-reset::-webkit-search-results-button, .input-reset::-webkit-search-results-decoration {
  display: none;
}

.visually-hidden {
  position: absolute;
  overflow: hidden;
  margin: -1px;
  border: 0;
  padding: 0;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
}

.container {
  margin: 0 auto;
  padding: 0 var(--container-offset);
  max-width: var(--container-width);
}

.centered {
  text-align: center;
}

.dis-scroll {
  position: fixed;
  left: 0;
  top: 0;
  overflow: hidden;
  width: 100%;
  height: 100vh;
  overscroll-behavior: none;
}

.page--ios .dis-scroll {
  position: relative;
}

.header {
  color: #fff;
  background-color: #010101;
}
.header .container .header__top {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 16px 0;
}
.header .container .header__top .call_link {
  color: var(--blue-color);
}

.logo {
  font-weight: 900;
  font-size: 28px;
  line-height: 75%;
  color: rgb(79, 143, 240);
}
.logo span {
  font-weight: 900;
  font-size: 20px;
  text-transform: lowercase;
  color: rgb(255, 255, 255);
}

.header__main {
  position: sticky;
  top: 0;
  padding: 22px 0 28px;
  color: #fff;
  background: rgba(1, 1, 1, 0.319);
}
.header__main .container {
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 20px;
}
.header__main .container nav ul {
  display: flex;
  list-style: none;
  gap: 32px;
}
.header__main .container nav ul li a {
  position: relative;
  text-decoration: none;
  color: inherit;
  transition: 0.4s;
}
.header__main .container nav ul li a::before {
  content: "";
  position: absolute;
  left: 0;
  bottom: 0;
  width: 100%;
  height: 1px;
  background-color: var(--blue-color);
  transform: scaleX(0);
  transform-origin: left;
  transition: 0.6s;
}
.header__main .container nav ul li a:hover {
  color: var(--blue-color);
}
.header__main .container nav ul li a:hover::before {
  transform: scaleX(1);
}

.header-phone-link, .header-mail-link {
  position: relative;
  margin-left: 45px;
}

.header-phone-link::before, .header-mail-link::before {
  position: absolute;
  left: -32px;
  top: -2%;
  width: 16px;
  height: auto;
}

.header-phone-link::before {
  content: url("../image/phone-icon.png");
}

.header-mail-link::before {
  content: url("../image/mail-icon.png");
}

.footer {
  padding: 80px 0 0;
  color: #fff;
  background-color: #222;
}
.footer .container .footer-top {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 20px;
  gap: 150px;
}
.footer .container .footer-top .logo {
  min-width: 276px;
}
.footer .container .footer-main {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  margin-top: 70px;
  margin-bottom: 70px;
  gap: 24px;
}
.footer .container .footer-main .footer-contacts {
  display: flex;
  flex-direction: column;
  padding-left: 0;
  gap: 24px;
}
.footer .container .footer-main .footer-contacts .footer-contacts-item {
  display: flex;
  align-items: center;
  gap: 24px;
}
.footer .container .footer-main .footer-contacts .footer-contacts-item img {
  padding: 12px 11px;
  background-color: #2C2C2C;
}
.footer .container .footer-main .footer-contacts .footer-contacts-item h3 {
  margin: 0;
  padding: 0;
  font-weight: 700;
  font-size: 12px;
  line-height: 120%;
  text-transform: uppercase;
  color: rgb(140, 140, 140);
}
.footer .container .footer-main .footer-contacts .footer-contacts-item h2 {
  margin: 0;
  font-weight: 700;
  font-size: 16px;
  line-height: 150%;
  color: rgb(255, 255, 255);
}
.footer .container .footer-main .navigation-footer ul {
  display: flex;
  flex-direction: column;
  list-style: none;
  padding-left: 0;
  gap: 24px;
}
.footer .container .footer-main .navigation-footer ul li a {
  font-weight: 700;
  font-size: 14px;
  line-height: 120%;
  text-transform: uppercase;
  transition: 0.5s;
}
.footer .container .footer-main .navigation-footer ul li a:hover {
  color: var(--blue-color);
}
.footer .container .footer-main .footer-comment {
  max-width: 424px;
}
.footer .container .footer-main .footer-comment .footer-avatar {
  display: flex;
  align-items: center;
  gap: 24px;
}
.footer .container .footer-main .footer-comment .footer-avatar h3 {
  margin: 0;
  margin-bottom: 8px;
}
.footer .container .footer-main .footer-comment .footer-avatar p {
  margin: 0;
}
.footer .container .footer-main .footer-comment .footer-comment-text {
  margin-bottom: 20px;
  font-weight: 400;
  font-size: 14px;
  line-height: 150%;
}
.footer .footer-bottom {
  margin-top: 30px;
  padding: 16px 0;
  background-color: #181818;
}
.footer .footer-bottom .container {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.footer ul {
  list-style: none;
}

.hero {
  margin-top: -101px;
  background-image: url("../image/background-hero.png");
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
}
.hero .container .hero__content {
  padding: 142px 0 345px;
  text-align: center;
}
.hero .container .hero__content .hero_subtitle {
  font-weight: 300;
  font-size: 30px;
  line-height: 150%;
  text-transform: uppercase;
  color: rgb(255, 255, 255);
}
.hero .container .hero__content .hero_title {
  font-weight: 600;
  font-size: 56px;
  line-height: 150%;
  text-transform: uppercase;
  color: rgb(79, 143, 240);
}
.hero .container .hero__content .hero_description {
  font-weight: 400;
  font-size: 20px;
  line-height: 120%;
  color: var(--light-color);
}

.our_services {
  background-color: rgb(247, 247, 247);
}

.our_services__list {
  display: grid;
  grid-template-columns: repeat(3, minmax(280px, 1fr));
  list-style: none;
  margin: 0;
  margin-top: 48px;
  padding: 0;
  row-gap: 20px;
}

.our_service-item {
  padding: 60px 32px;
  max-width: 424px;
  text-align: center;
  background-color: #FFF;
}
.our_service-item img {
  max-width: 35px;
  max-height: 35px;
}
.our_service-item h3 {
  margin-bottom: 0;
  font-weight: 800;
  font-size: 20px;
  line-height: 150%;
  color: rgb(34, 34, 34);
}
.our_service-item p {
  font-weight: 400;
  font-size: 14px;
  line-height: 120%;
  color: rgb(34, 34, 34);
}

.about .container {
  display: flex;
}
.about .container .about-content h2 {
  margin-bottom: 60px;
}
.about .container .about-content img {
  width: 100%;
  max-width: 563px;
}
.about .container .about-content p {
  font-weight: 400;
  font-size: 16px;
  line-height: 150%;
  color: rgb(34, 34, 34);
}
.about .container .about-content .btn-flex {
  display: flex;
  margin-top: 32px;
  gap: 24px;
}

.team_list {
  display: flex;
  list-style: none;
  margin-top: 30px;
  padding-left: 0;
  gap: 24px;
}
.team_list .team-item {
  max-width: 312px;
  text-align: center;
  background-color: #fff;
}
.team_list .team-item .overflow-hidden {
  position: relative;
  z-index: 10;
}
.team_list .team-item .team-item-content {
  position: relative;
  z-index: 50;
  overflow: hidden;
  margin-top: -5px;
  padding: 24px 16px;
  background-color: #fff;
}
.team_list .team-item h2 {
  margin-bottom: 10px;
  font-weight: 700;
  font-size: 16px;
  line-height: 120%;
}
.team_list .team-item p {
  margin-top: 0;
  font-weight: 300;
  font-size: 14px;
  line-height: 120%;
}

.reviews {
  background-image: url("../image/background-reviews.png");
  background-size: cover;
  background-repeat: no-repeat;
}

.reviews-list {
  display: flex;
  justify-content: space-between;
  list-style: none;
  margin: 48px 0 32px;
  padding-left: 0;
  gap: 24px;
}
.reviews-list .review-item {
  padding: 32px;
  max-width: 648px;
  background-color: #FFF;
}
.reviews-list .review-item .review_top {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.reviews-list .review-item .review_top .review_top-avatar {
  display: flex;
  align-items: center;
  gap: 24px;
}
.reviews-list .review-item .review_top .review_top-avatar div h3 {
  margin-bottom: 4px;
  font-weight: 300;
  font-size: 14px;
  line-height: 120%;
}
.reviews-list .review-item .review_top .review_top-avatar div h2 {
  margin-top: 0;
  font-weight: 700;
  font-size: 20px;
  line-height: 150%;
}
.reviews-list .review-item .review_main p {
  font-weight: 400;
  font-size: 14px;
  line-height: 150%;
}

.posts_list {
  display: grid;
  grid-template-columns: repeat(3, minmax(300px, 424px));
  list-style: none;
  margin-top: 30px;
  margin-left: 0;
  padding-left: 0;
  gap: 24px;
}
.posts_list .posts_item a img {
  max-width: 424px;
  max-height: 270px;
}
.posts_list .posts_item a .post-title {
  font-weight: 700;
  font-size: 16px;
  line-height: 150%;
}
.posts_list .posts_item a .post-date {
  font-weight: 300;
  font-size: 12px;
  line-height: 120%;
  color: rgb(34, 34, 34);
}
.posts_list .posts_item a .post-desc {
  font-weight: 400;
  font-size: 14px;
  line-height: 120%;
}

a {
  color: inherit;
}

.block {
  padding: 80px 0;
}

.title-block {
  position: relative;
  font-weight: 800;
  font-size: 28px;
  line-height: 150%;
  text-transform: uppercase;
  color: rgb(34, 34, 34);
}
.title-block.title-left::before {
  left: calc(0% + 40px);
}
.title-block::before {
  content: "";
  position: absolute;
  left: 50%;
  bottom: -20px;
  width: 80px;
  height: 3px;
  background-color: var(--blue-color);
  transform: translateX(-50%);
}

.subtitle-block {
  margin-top: 20px;
  max-width: 900px;
  font-weight: 400;
  font-size: 16px;
  line-height: 120%;
  color: rgb(34, 34, 34);
}

.center-block {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
}

.btn {
  border: 0;
  padding: 16px 24px;
  text-transform: uppercase;
  cursor: pointer;
}
.btn.btn--blue {
  border: 2px solid rgb(79, 143, 240);
  color: var(--light-color);
  background-color: var(--blue-color);
}
.btn.btn--blue:hover {
  background-color: var(--dark-blue-color);
}

.overflow-hidden {
  overflow: hidden;
}
.overflow-hidden img {
  transition: transform 0.5s ease;
}
.overflow-hidden img:hover {
  transform: scale(1.15);
}